// reset everything
* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

html {
  // reset the font size to 10px
  font-size: 62.5%;

}

html,
body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.content {
  // margin-top: 5.2rem;
}

.header__wrapper {
