  h1 {
    color: $color-white;
    font-size: 3.6rem;
    font-weight: normal;
    padding: 1rem 1.5rem;
    text-align: center;
  }
}

.header__body {
  background-image: url('asset/image/robertoblake2016header.jpg');
  height: 61.1rem;
  background-position: center center;
  background-size: cover;

}